import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";

import { PersonsService } from './persons.service';

import { PersonAddComponent } from './person-add/person-add.component';
import { PersonsComponent } from './persons/persons.component';
import { PersonUpdateComponent } from './person-update/person-update.component';

const PERSONS_ROUTES: Routes = [
  { path: 'persons', component: PersonsComponent },
  { path: 'persons/add', component: PersonAddComponent },
  { path: 'persons/update/:id', component: PersonUpdateComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(PERSONS_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule
  ],
  declarations: [PersonAddComponent, PersonsComponent, PersonUpdateComponent, PersonUpdateComponent],
  providers: [PersonsService],
  exports: [PersonsComponent]
})
export class PersonsModule { }


